.table-responsive{
    height:500px;  
    overflow:scroll;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  }
   thead tr:nth-child(1) th{
      background: rgb(52, 224, 30);
      position: sticky;
      top: 0;
      z-index: 10;
    }

    .container-fluid {

        width: 90%;
        
        margin: auto;
        
        }